import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import picturepost from '../views/picturepost.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/picturepost',
    name: 'picturepost',
    component: picturepost,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
